    <hr id="cf7cb297-9933-40a6-9857-751129ad19bb" />
    <h3
      id="e64f895f-e513-4a73-bd3b-d9888486182f"
      class="block-color-yellow_background"
    >
      (1) 서비스 소개
    </h3>
    <figure
      id="70553981-8488-4bc7-bbce-938d52ba1c71"
      class="image"
      style="text-align: center"
    >
      <a
        href="https://cdn.discordapp.com/attachments/1089853488982401046/1089890503526453268/d770cbb64dfa58af.gif"
        ><img
          style="width: 384px"
          src="https://cdn.discordapp.com/attachments/1089853488982401046/1089890503526453268/d770cbb64dfa58af.gif"
      /></a>
    </figure>
    <figure
      class="callout"
      style="white-space: pre-wrap; display: flex"
      id="482540df-0fb6-4ab7-89bc-8f488baa3fba"
    >
      <div style="font-size: 1.5em"><span class="icon">🍻</span></div>
      <div style="width: 100%">
        <strong
          ><mark class="highlight-orange_background"
            ><em style="letter-spacing: 1px; line-height: 2"
              >'우리가 흔히 마시는 잭다니엘, 잭다니엘은 얼마나 벌었을까?'
              <br />
              '이제 하다 못해 루이비통에서 까지 술을 만든다?'
              <br />
              '양주보다 소주의 숙취가 더 쎄다?'
              <br />
              '무알콜 맥주를 미성년자가 마셔도 될까?' <br />
              '무알콜 맥주를 마시고 운전을 해도 될까?’ <br />
              '감기에 소주 반 잔, 체했을 때 소주 한 잔, 진짜일까?’</em
            ></mark
          ></strong
        >

        <p id="aa89f4c3-f03c-44d6-9379-337fd742fc02" class="">
          <strong style="line-height: 3; font-size: 1.5rem"
            >혹시 위 질문들 중 궁금한 것이 있나요?</strong
          >
          <br />
          모르고 마시면 알콜향이지만, 알고 마시면 느껴지는 술의 맛과 향.
          오마이술은 그냥 술만 마시는 사람을 “술잘알”로 만들어 드립니다. 다양한
          취향을 가진 동물 회사원이 다양한 술과 맛집에 대한 찐 리뷰를 알려주며,
          쉽고 저렴하게 마시는 방법, 직접 체험한 맛집, 술과 관련된 재미있는
          썰까지 한번에 알려드립니다. 사람들의 호기심을 자극하는 질문들, 그
          질문을 해결해주는 공간. 다양한 주류 콘텐츠로 새로운 술에 도전하는
          사람들에게 주류 문화와 제품에 대한 정보를 제공하는 뉴스레터 ‘오 마이
          술!’입니다.
        </p>
      </div>
    </figure>
    <figure
      class="callout"
      style="white-space: pre-wrap; display: flex"
      id="3b6ed1c2-663c-4d42-b142-89caaad827ea"
    >
      <div style="font-size: 1.5em"><span class="icon">🍻</span></div>
      <div style="width: 100%">
        <mark class="highlight-yellow_background"><strong>Value</strong></mark
        ><strong> 흩어져 있던 술과 관련된 정보를 취합하여 제공</strong>
        다양한 술 소개 및 테이스팅 후기를 통하여 개인의 취향에 맞는 술을 찾을 수
        있음
        <strong>새로운 주종을 시도해 볼 수 있도록 다양한 선택지 제공</strong>
        술 브랜드의 종류 및 이야기를 알 수 있음 다양한 술과 관련된 호기심을
        해결할 수 있음
      </div>
    </figure>
    <figure
      class="callout"
      style="white-space: pre-wrap; display: flex"
      id="c17f474e-3fd0-4bfd-bb57-a674a31fda3b"
    >
      <div style="font-size: 1.5em"><span class="icon">🍻</span></div>
      <div style="width: 100%">
        <mark class="highlight-yellow_background"><strong>Cost</strong></mark>
        뉴스레터를 탐색하는 시간 구독 및 메일을 열람 하는 시간 개인 정보
        제공(이메일 주소) 확인하지 않는 메일함, 쌓여가는 뉴스레터
      </div>
    </figure>
