    <p id="9d474cd6-44f6-453b-bd26-feea078b00da" class=""></p>
    <div class="indented">
      <p
        id="9f802cba-29d7-4ace-9271-c63e1897940d"
        class="block-color-gray_background"
      >
        <strong>인상 깊었던 점</strong>
      </p>
    </div>
    <p></p>
    <ul id="bfb9f3be-0f90-41c6-93b5-08557fd2afef" class="toggle">
      <li>
        <details open="">
          <summary>소재에 따라 남녀 도달수에 차이를 보임</summary>
          <ul id="d38f6a6c-b1f1-4e6e-be22-26b19a5e7e19" class="bulleted-list">
            <li style="list-style-type: disc">
              1차 ox 퀴즈 광고의 경우 남성과 여성 성비 유입율(10:1)이 극명하게
              나뉘었다. 이후 2차 광고에서 남성이 더 흥미를 보일 수 있을만한
              ‘운전’과 관련된 소재를 ‘무알콜, 청소년’ 소재의 퀴즈로 대체하였지만
              노출 수 남성 4,718/여성 1,623, 클릭수 남성 269/여성 104를 기록하며
              여전히 남성 비율이 높았다.
            </li>
          </ul>
          <ul id="7e7a245d-8c83-42fc-89dc-7be49e34eafc" class="bulleted-list">
            <li style="list-style-type: disc">
              하이볼 소재의 경우 여성 도달수가 더 높음. 여성의 유입수를 높이기
              위해서는 ‘하이볼’, ‘칵테일’ 소재를 이용한 광고
            </li>
          </ul>
          <ul id="f5aa8b45-e0c3-4bb3-9edb-777364047b22" class="bulleted-list">
            <li style="list-style-type: disc">
              같은 주류에 대한 카피더라도 ‘혼술, 가성비’의 경우 남성의 도달수가
              높았고, ‘연인’의 경우 여성의 도달수가 높았다. → 여성을 대상으로
              하는 주류광고의 경우 카피를 설정할 때 ‘연인’ 혹은 ‘함께’ 등과 같은
              문구가 들어가는 것이 좋을 것 같다.
            </li>
          </ul>
          <ul id="b16621f3-f271-45bb-981d-4cc65f6baa8a" class="bulleted-list">
            <li style="list-style-type: disc">
              술 자체의 관심도는 남성이 높음
            </li>
          </ul>
        </details>
      </li>
    </ul>
    <ul id="df3dd564-40f4-483b-acfa-97cf1780142b" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong
          >하이볼 소재의 경우 노출 수는 남성(800)에 비해 여성(1100)이 높았지만,
          클릭 CTR이 남성이 더 높게 집계되었다. 술 관련 소재는 남성들에게 더
          흥미를 이끌 수 있다고 보여진다.
        </strong>
      </li>
    </ul>
    <ul id="f0ba282a-03e0-49be-aedd-25141ba826a3" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong
          >트래픽을 위한 광고에 전환을 위한 소재를 섞기 보다는 따로 캠페인을
          진행하고, 트래픽 캠페인으로 유입된 고객을 전환으로 유도하기 위한
          장치를 랜딩페이지에 별도로 마련하는 것이 좋았을 것 같다.</strong
        >
      </li>
    </ul>
    <ul id="185a9e85-747a-40be-b6c0-6c6fe810b1e3" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong
          >성과가 좋은 카피라고 하더라도 광고 형식에 따라, 그리고 카피를
          조합하는 법에 따라 광고 효율이 달라질 수 있다.</strong
        >
      </li>
    </ul>
    <ul id="1f63751f-100b-466a-88c4-d379b0fa0f77" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong>글이 너무 많으면 광고 효율이 떨어진다.</strong>
      </li>
    </ul>
    <p id="e8f2f8d1-5ecb-4b1d-9095-9ef52fb8b05a" class=""></p>
    <div class="indented">
      <p
        id="00fa80b1-9b97-4b7d-8da5-9f8664e9e081"
        class="block-color-gray_background"
      >
        <strong>3차 광고에서 시도/검증해보고 싶은 부분</strong>
      </p>
      <ul id="5abab583-e103-490f-8846-9e0bc49d9b13" class="bulleted-list">
        <li style="list-style-type: disc">
          성과를 입증한 광고 카피인 혼술과, 가성비 위스키&amp;와인을 디벨롭 하여
          소재 제작
        </li>
      </ul>
      <ul id="fdd71e5d-b9d4-48d2-987b-dbebbb51b68b" class="bulleted-list">
        <li style="list-style-type: disc">
          ox 퀴즈의 소재를 뉴스레터 콘텐츠와 관련지어 설정
        </li>
      </ul>
    </div>
