      >
        <strong>느낀 점 &amp; 개선 사항</strong>
      </p>
      <ul id="8a106a85-22b3-4321-bdbf-94fe7b50bfff" class="bulleted-list">
        <li style="list-style-type: disc">
          광고 이미지 제작 시 모바일 앱에 최적화 되도록 제작
        </li>
      </ul>
      <ul id="a965a45d-fa0a-4f53-8bcb-4cd4a7158706" class="bulleted-list">
        <li style="list-style-type: disc">
          구글 광고는 입찰가가 높아지더라도 모바일 광고만 진행
        </li>
      </ul>
      <ul id="b003290b-5794-451e-97de-10148e5d3a00" class="bulleted-list">
        <li style="list-style-type: disc">
          구글 광고는 좀 더 타겟을 좁힐 수 있도록 검색 광고를 진행
        </li>
      </ul>
      <ul id="0fc7dfdf-55ad-43ac-9461-8a73b9204d7a" class="bulleted-list">
        <li style="list-style-type: disc">
          구글 디스플레이 광고를 할 경우 키워드 타겟팅을 진행
        </li>
      </ul>
    </div>
