    <div class="indented">
      <p
        id="2aaa4fa6-7c4c-4115-bc63-1abd46357956"
        class="block-color-gray_background"
      >
        <strong>느낀 점 &amp; 개선 사항</strong>
      </p>
      <ul id="dff53f8f-be33-4a31-b5c3-d01f2e2f0da8" class="bulleted-list">
        <li style="list-style-type: disc">
          FB/IG 광고를 게시할 때, 문구 작성 등은 인스타그램 피드를 기준으로 작성
        </li>
      </ul>
      <ul id="a977a6f6-241f-4454-800f-acac46ac6a21" class="bulleted-list">
        <li style="list-style-type: disc">
          인스타그램 광고를 통해 오마이술 계정으로 유입되는 고객들을 잡기 위해
          인스타그램 계정에 뉴스레터 콘텐츠 관련 게시글을 주기적으로 업데이트
        </li>
      </ul>
    </div>
    <p></p>
    <p id="9186b940-4589-4c2d-9727-1d6363049279" class=""></p>
    <h3
      id="5ef1797e-a83c-405a-8ac1-5a041633ba99"
      class="block-color-yellow_background"
    >
      2) 문제 분석
    </h3>
    <ul id="44f0843a-8cfc-4746-b6bd-befefdfac650" class="bulleted-list">
      <li style="list-style-type: disc">
        ox 퀴즈에서 유입된 고객 중 노이즈가 발생
      </li>
    </ul>
    <ul id="86485a0d-fa4f-4bfe-9088-b4e04b232fa8" class="bulleted-list">
      <li style="list-style-type: disc">
        트래픽 캠페인이었던 ox 퀴즈 광고를 통한 전환수가 전환 광고 캠페인을 통한
        전환수보다 오히려 높게 측정
      </li>
    </ul>
    <ul id="b03df018-5c47-4b3c-9cad-2c730c457f5f" class="bulleted-list">
      <li style="list-style-type: disc">
        광고 이미지를 모바일에서 봤을 때, 폰트의 효과를 추가해서 가독성 저하
      </li>
    </ul>
    <ul id="49698d6f-ca9a-4845-a5f0-3e67a40212f9" class="bulleted-list">
      <li style="list-style-type: disc">
        non-paid 채널에서의 구독 완료 수와 paid 채널에 따른 구독 완료 수가 크게
        차이가 나지 않음
      </li>
    </ul>
    <ul id="a3c34051-bc33-4abe-ad0c-dc44c1013d94" class="bulleted-list">
      <li style="list-style-type: disc">
        예산에 비해 너무 많은 소재와 광고 세트를 설정하여 페이스북 광고의
        머신러닝이 광고 진행 기간동안 계속 진행됨
      </li>
    </ul>
    <p id="bf910c33-68a8-4f4d-8641-1380d5f092c0" class=""></p>
    <h3
      id="3bbdc020-69ef-4e4e-8204-c3ead412bb27"
      class="block-color-yellow_background"
    >
      3) 개선 제안
    </h3>
    <ul id="d9ce5fb6-5322-4b0f-af5c-f90251e5f877" class="bulleted-list">
      <li style="list-style-type: disc">
        노이즈를 막으면서 유입을 높일 수 있는 소재를 고민해봐야 함
      </li>
    </ul>
    <ul id="ea5b0a14-6a47-4619-bfcf-0d5e377635b6" class="bulleted-list">
      <li style="list-style-type: disc">참여형 광고 소재의 다양화</li>
    </ul>
    <ul id="5ee2a388-33d6-4e96-b2df-ae6e931cf389" class="bulleted-list">
      <li style="list-style-type: disc">
        전환을 목적으로 하는 캠페인도 광고 목표 설정을 트래픽으로 설정
      </li>
    </ul>
    <ul id="7f361cc4-7941-4951-819f-977b2bcde762" class="bulleted-list">
      <li style="list-style-type: disc">
        광고 이미지 제작 시 인스타그램 피드 화면에 합성해서 모바일 기기로 확인
      </li>
    </ul>
    <ul id="3b7b1f4b-a1fa-480c-881d-7b509a3fda42" class="bulleted-list">
      <li style="list-style-type: disc">
        술 관련 커뮤니티 등 다양한 non-paid 채널 탐색
      </li>
    </ul>
    <ul id="cc9aae96-ef6e-4978-a555-aa43e5d517dd" class="bulleted-list">
      <li style="list-style-type: disc">
        paid 광고 집행 시에도 타겟을 좁혀서 광고를 진행
      </li>
    </ul>
    <ul id="48d357dd-7af4-464e-afee-71a092108f7b" class="bulleted-list">
      <li style="list-style-type: disc">
        캠페인 당 광고 소재 수 3~5개 내외로 설정
      </li>
    </ul>
    <p id="9b5e46b7-cff6-4562-93c0-bbed2c5634d0" class=""></p>
    <h3
      id="67fed85d-fe5d-445a-8e54-56dbb3b46ce3"
      class="block-color-yellow_background"
    >
      4) 실행 계획
    </h3>
    <ul id="3835e96a-02bb-4ed4-8e54-534217a78f26" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong>3차 광고 : 2022.07.14 ~ 07.22 (7일)</strong>
      </li>
    </ul>
    <ul id="4ce1487a-8e8c-4342-8a87-04e902eb28ca" class="bulleted-list">
      <li style="list-style-type: disc">
        술에 관심이 있는 사람들을 대상으로 타겟을 좁혀서 광고를 진행 (구글
        애즈-키워드)
      </li>
    </ul>
    <ul id="6423c668-7140-44ff-aece-929ce1c576ec" class="bulleted-list">
      <li style="list-style-type: disc">
        FB/IG 광고 집행 시, 20대~30대로 나이 대만 좁혀서 광고를 진행
      </li>
    </ul>
    <ul id="b2241bd8-5e7a-4005-9f28-cf26df9aa335" class="bulleted-list">
      <li style="list-style-type: disc">
        FB/IG 전환 목표 캠페인을 수행 시, 같은 광고 이미지로 트래픽 목표
        캠페인을 수행
      </li>
    </ul>
    <ul id="098886fc-b0d8-42bb-aec9-4a24b6054626" class="bulleted-list">
      <li style="list-style-type: disc">
        2차 광고 소재 카피를 디벨롭하여 A/B테스트 진행
        <ul id="6e764997-6176-4297-b47a-a1535ed9023b" class="bulleted-list">
          <li style="list-style-type: circle">
            가성비, 혼술, 위스키, 와인, 하이볼 등 고성과 광고에 들어간 키워드
            재조합
          </li>
        </ul>
        <ul id="3e396cb4-c35b-4de0-92f8-cca4465e953a" class="bulleted-list">
          <li style="list-style-type: circle">
            후기 광고는 좀 더 매력적인 형식과 카피 고민
          </li>
        </ul>
        <ul id="2ae26bc3-9009-4a27-bdfd-96944814b723" class="bulleted-list">
          <li style="list-style-type: circle">
            팀원 사전 투표 진행해 캠페인당 소재 5개 이내로 제한
          </li>
        </ul>
        <ul id="9ccfc359-a34f-4211-8a89-627234008f90" class="bulleted-list">
          <li style="list-style-type: circle">
            저성과 광고 소재는 머신러닝 기간을 감안하며 3차에는 진행 하지 않음
          </li>
        </ul>
      </li>
    </ul>
    <ul id="8b4a27f6-a572-477b-9506-23521358ba6b" class="bulleted-list">
      <li style="list-style-type: disc">
        참여형 광고 집행 시, 소재 다양화 및 기존 소재를 변경하여 진행
        <ul id="50654e03-c9b4-426d-88bf-8bfb2cbe4b3b" class="bulleted-list">
          <li style="list-style-type: circle">
            술MBTI, 술 스타일(술타일) 찾기 등 술 콘텐츠에 흥미를 느낄 수 있는
            소재로 집행
          </li>
        </ul>
        <ul id="f400b26a-917a-433f-a4fd-e53a4189207d" class="bulleted-list">
          <li style="list-style-type: circle">
            유입 노이즈를 막기 위해 ox 퀴즈의 소재를 뉴스레터 콘텐츠와 관련 지어
            설정
          </li>
        </ul>
      </li>
    </ul>
    <ul id="b67ef442-95b3-40c7-b97d-aabdf75ac1a1" class="bulleted-list">
      <li style="list-style-type: disc">
        네이버 카페, 블로그의 술 후기+뉴스레터 홍보 게시글을 작성, 트위터에서
        홍보를 진행
      </li>
    </ul>
    <ul id="a84a4186-eebb-4c2e-aa8c-c97d48c50798" class="bulleted-list">
      <li style="list-style-type: disc">
        고객인터뷰를 통한 인사이트를 광고 소재로 활용하여 진행
        <ul id="8a1e632d-cc62-42c7-a1d8-4ebb704a1dff" class="bulleted-list">
          <li style="list-style-type: circle">
            뉴스레터 콘텐츠 관련한 소재
            <ul id="43c56e80-10a3-4205-85c6-6ea219efd36a" class="bulleted-list">
              <li style="list-style-type: square">
                입문자를 위한 칵테일바 이용 방법
              </li>
            </ul>
            <ul id="a3ac393b-cf9e-4db1-b67c-6921a2ea0668" class="bulleted-list">
              <li style="list-style-type: square">
                싱글몰트 안에서도 나눠지는 다양한 종류의 위스키 소개
              </li>
            </ul>
          </li>
        </ul>
        <ul id="b9f675fe-fbac-4ad2-b888-6e62d09e8ce6" class="bulleted-list">
          <li style="list-style-type: circle">
            신상주류에 대한 궁금증과 관련된 소재
            <ul id="97177f27-8a80-45ab-89d8-20f19b83f507" class="bulleted-list">
              <li style="list-style-type: square">
                원소주,빡치주와 같은 mz세대들에게 핫한 증류주 맛 소개
              </li>
            </ul>
            <ul id="5eb0551a-c09b-4841-9cb3-aedcb6187bf8" class="bulleted-list">
              <li style="list-style-type: square">
                다양한 맥주 종류들중에 내 취향에 맞는 맛있는 맥주 고르는 방법
                소개
              </li>
            </ul>
          </li>
        </ul>
      </li>
    </ul>
    <h2
