      <strong>1차 랜딩페이지 문제점</strong>
    </blockquote>
    <ul id="89eb83cb-d250-4d7a-b365-92e9f47df55e" class="bulleted-list">
      <li style="list-style-type: disc">
        단일 페이지에 많은 메시지를 담음 (ux의 문제와 연결)
      </li>
    </ul>
    <ul id="1bcb7adf-388e-45c1-8ebb-c65973c71a9f" class="bulleted-list">
      <li style="list-style-type: disc">구독 CTA 유도 메세지 부족함.</li>
    </ul>
    <ul id="a0f25df2-3645-4d33-80ee-c0641f1f529f" class="bulleted-list">
      <li style="list-style-type: disc">
