      class="block-color-gray_background"
    >
      <strong>인상 깊었던 점</strong>
    </p>
    <ul id="45cd1e86-ea9b-424e-95ec-b7e24c1d828c" class="bulleted-list">
      <li style="list-style-type: disc">
        오전 7시, 오후 5시 이후에 오픈율이 가장 높다.
        <ul id="ce11802f-6de7-4f87-bd11-488bc82ee364" class="bulleted-list">
          <li style="list-style-type: circle">
            출퇴근 시간에 주로 확인한다는 것을 발견함.
          </li>
        </ul>
      </li>
    </ul>
    <ul id="3a094a30-70db-4440-9bf5-628bd0b5fe18" class="bulleted-list">
      <li style="list-style-type: disc">
        모바일보다는 데스크탑 오픈이 약 2배 이상으로 높았음.
      </li>
    </ul>
    <ul id="f5b3bfe0-d0eb-4a5e-b3d0-cb9f2a32496a" class="bulleted-list">
      <li style="list-style-type: disc">
        주제가 흥미로웠다는 구독자 피드백이 2건 이상 발견됨.
      </li>
    </ul>
    <p id="30d3a36a-35a4-4702-a8e9-094d25ac6ce8" class=""></p>
    <h3
      id="a8d41400-7ccc-47ab-acc5-c20bc701b77f"
      class="block-color-yellow_background"
    >
      2) 문제 분석
    </h3>
    <ul id="66ac3cdc-5881-44d6-8234-6342da4763eb" class="bulleted-list">
      <li style="list-style-type: disc">랜딩페이지 톤앤매너 수정이 발생.</li>
    </ul>
    <ul id="53c57d29-9158-4cfb-8a8f-be9600da92ca" class="bulleted-list">
      <li style="list-style-type: disc">
        달라진 톤앤매너, 글씨 크기, 용어 정리 추가, 길이로 인한 메일 잘림
      </li>
    </ul>
    <ul id="dd16b790-bf40-46ac-8531-02c8a9b17a66" class="bulleted-list">
      <li style="list-style-type: disc">문단이 붙어있어 가독성이 떨어짐</li>
    </ul>
    <ul id="854e30bc-0b70-475a-b99c-a41fd2d725e6" class="bulleted-list">
      <li style="list-style-type: disc">내용이 길어서 지루하게 느껴짐</li>
    </ul>
    <ul id="e962e74e-92fa-4385-a2d0-7db2d7f59613" class="bulleted-list">
      <li style="list-style-type: disc">
        첫 뉴스레터 발행 시, 타입폼 용량 제한 이슈가 발생되어 구독자들의
        피드백이 제대로 모이지 않았음.
      </li>
    </ul>
    <p id="9b7bbc13-8f53-4dde-8189-550465b038e1" class=""></p>
    <h3
      id="18d2758f-6466-46ce-a7fb-b9686f4d63d6"
      class="block-color-yellow_background"
    >
      3) 개선 제안
    </h3>
    <ul id="b0773d9e-951c-4a5a-8d48-9b737a8a07ff" class="bulleted-list">
      <li style="list-style-type: disc">
        랜딩페이지의 달라진 톤앤매너(오렌지→블루)에 따라 2차 뉴스레터의 색상도
        블루톤으로 변경함
      </li>
    </ul>
    <ul id="87ab012d-4bcb-470d-bfbb-1870bd0e8892" class="bulleted-list">
      <li style="list-style-type: disc">
        글자가 작아 잘 보이지 않았던 부분에 대하여 글씨 크기를 키우고, 본문
        내용을 줄여 전체적인 길이감에는 문제가 없도록 개선함
      </li>
    </ul>
    <ul id="fe63fdb9-97d5-4911-b594-6e540a7f936c" class="bulleted-list">
      <li style="list-style-type: disc">
        본문의 길이가 길어 ‘더보기’를 눌러야 하단까지 확인이 가능했으며, 글의
        내용에서 긴장감이 떨어지는 문제점을 본문의 길이를 줄이고 긴장감을 주는
        전개를 통해 개선함
      </li>
    </ul>
    <ul id="0da3c6cb-8f67-4bdc-aa6c-8eb585c46bd3" class="bulleted-list">
      <li style="list-style-type: disc">
        가독성을 높이기 위하여 단락 띄어쓰기, 문단 구분 등을 개선함
      </li>
    </ul>
    <ul id="ebcb386d-6e1e-4cee-9d53-4fd24472a9aa" class="bulleted-list">
      <li style="list-style-type: disc">
        어려운 용어로 인하여 이해가 어려운 부분에 대하여 본문 하단에 용어 정리를
        통하여 개선함
      </li>
    </ul>
    <p id="24ff7d1c-21bc-4374-9c33-0d81c1922c15" class="block-color-gray"></p>
    <h3
      id="8cef2313-520a-458e-a4af-21db68eac73c"
      class="block-color-yellow_background"
    >
      4) 실행 계획
    </h3>
    <ul id="058e5806-86e2-4207-9911-471d8aafa163" class="bulleted-list">
      <li style="list-style-type: disc">
        랜딩페이지의 톤앤매너에 맞추어 뉴스레터의 색상도 블루톤으로 변경할 예정
      </li>
    </ul>
    <ul id="de5551cb-b979-4de4-8035-51a24189a12b" class="bulleted-list">
      <li style="list-style-type: disc">
        글자 크기를 모바일 버전에 맞게 좀 더 키우고, 대화형 이미지 크기 또한
        조정할 예정
      </li>
    </ul>
    <ul id="d77a5e6d-4b28-4ba4-8df8-77a70dbff3ec" class="bulleted-list">
      <li style="list-style-type: disc">
        본문의 길이를 조절하여 메일 페이지에서 한 번에 확인이 가능하도록 조정할
        예정
      </li>
    </ul>
    <ul id="811e5d5f-b9b0-4588-810a-517aa2cbe6e0" class="bulleted-list">
      <li style="list-style-type: disc">
        단락 띄어쓰기, 문단 구분선을 추가하여 가독성을 높일 계획
      </li>
    </ul>
    <ul id="32b540ab-1376-423b-9209-68334ceb227e" class="bulleted-list">
      <li style="list-style-type: disc">
        2차 뉴스레터 발송 시, 어려운 주류 용어는 하단에 쉽게 설명 추가
      </li>
    </ul>
    <ul id="10bb5001-4bc0-47b2-ad22-e322fa3e87bb" class="bulleted-list">
      <li style="list-style-type: disc">
        타입폼은 월 구독을 해야하기 때문에 무료로 응답을 받을 수 있는 구글
        폼즈로 변경
      </li>
    </ul>
