    <ul id="c4a4c6c8-70fe-4665-9a75-642b50e8dfcb" class="bulleted-list">
      <li style="list-style-type: disc">
        오픈율 50%대를 유지하고 있지만 2%~3%가량 꾸준히 낮아지고 있음
      </li>
    </ul>
    <ul id="babf16ba-0551-4342-af58-e7fb402addb4" class="bulleted-list">
      <li style="list-style-type: disc">
        수요일은 오후에 오픈이 거의 안됨(당일 인터뷰 요청을 추가로 발송했기
        때문으로 예상)
      </li>
    </ul>
    <ul id="dac6f569-4774-4f04-a7a9-b506dff0ab7d" class="bulleted-list">
      <li style="list-style-type: disc">
        뉴스레터 하단 피드백에서 부정 피드백 1건 긍정 피드백 7건 받음
      </li>
    </ul>
    <ul id="19245581-2d89-4eaf-9159-db7581d50d40" class="bulleted-list">
      <li style="list-style-type: disc">
        주제가 흥미롭다, 추천한 술이 궁금해졌다는 피드백이 각각 5건으로 가장
        많았음
