    <p id="b4b730a3-379b-4a7a-99ee-b56c0de0dd95" class=""></p>
    <h3
      id="de9fca67-f896-4f30-93bb-fd0483bc5ca5"
      class="block-color-yellow_background"
    >
      2) 문제 분석
    </h3>
    <ul id="e4d92f71-18a2-47f8-8abf-3a07ba03b5da" class="bulleted-list">
      <li style="list-style-type: disc">
        팀원 대화 이미지의 배경이 투명하여 글씨가 잘 보이지 않음
      </li>
    </ul>
    <ul id="04a2c9d8-f887-4dfc-b27f-91e980b73ca7" class="bulleted-list">
      <li style="list-style-type: disc">
        관심 주종 이외의 정보에 흥미를 느끼지 못하는 고객 존재
      </li>
    </ul>
    <ul id="b607b573-5406-4e8c-b98f-7a30445108da" class="bulleted-list">
      <li style="list-style-type: disc">
        1주차 63-59% &gt; 2주차 54-51%로 오픈율이 다소 감소하였고, 눈길을 끄는
        제목 선정이 필요함
      </li>
    </ul>
    <p id="c9c7f088-5d42-4c6a-97e1-74be88abce99" class=""></p>
    <h3
      id="ffc8a7d8-0a2a-4c3f-9fb1-b0caa76cce41"
      class="block-color-yellow_background"
    >
      3) 개선 제안
    </h3>
    <ul id="08484b19-605c-4162-a68b-14ec907add8c" class="bulleted-list">
      <li style="list-style-type: disc">
        뉴스레터 배경 색상을 흰색으로 변경하여 이미지 글자가 잘 보이지 않는 것을
        개선
      </li>
    </ul>
    <ul id="913177a5-0b85-4160-b231-2c73b173ab03" class="bulleted-list">
      <li style="list-style-type: disc">
        고객의 관심 주종이 각각 다르다는 것을 고려하여 다양한 주종을 전달할 수
        있도록 노력
      </li>
    </ul>
    <ul id="4faacbd8-e774-4cef-8aa2-dbf1d9418902" class="bulleted-list">
      <li style="list-style-type: disc">
        호기심을 유발하는 제목을 선정하여 오픈율을 높이는 것이 필요.
      </li>
    </ul>
    <ul id="8794d000-98f4-4a04-931c-9f29d4dcc604" class="bulleted-list">
      <li style="list-style-type: disc">
        구독자들이 잘 모르지만 흥미를 가질만한 소재 탐색 필요.
      </li>
    </ul>
    <p id="b42fe694-9ec5-4141-bc85-a44df5b7b572" class=""></p>
    <h3
      id="21f40b62-d0b4-4a7f-b698-8947a5d25eb3"
      class="block-color-yellow_background"
    >
      4) 실행 계획
    </h3>
    <ul id="82e15596-ee87-449f-b6e6-b1e042f099f4" class="bulleted-list">
      <li style="list-style-type: disc">
        제목 A/B 테스트 진행
        <ul id="68232a25-7005-4744-9ecc-591e361875ee" class="bulleted-list">
          <li style="list-style-type: circle">
            뉴스레터 내용과 연관 되면서 호기심을 유발하는 제목 2가지 작성
          </li>
        </ul>
        <ul id="255d0042-8225-4182-abe2-c71ecb213a7f" class="bulleted-list">
          <li style="list-style-type: circle">
            스티비 A/B테스트 기능을 이용해 성과 분석
          </li>
        </ul>
      </li>
    </ul>
    <ul id="fb63719f-6038-40cf-8149-d1d4f7f89a6a" class="bulleted-list">
      <li style="list-style-type: disc">
        뉴스레터 발송 시 인스타그램 업로드 하여 고객에게 알림 및 홍보
      </li>
    </ul>
    <ul id="b227fe71-8e25-4edf-987f-54958fd776f0" class="bulleted-list">
      <li style="list-style-type: disc">
        관심있는 주종에 대한 정보를 2주에 한 번은 받아볼 수 있도록 콘텐츠를 구성
      </li>
    </ul>
    <ul id="1444bce8-1c81-44c6-8fcf-84661e3a53f6" class="bulleted-list">
      <li style="list-style-type: disc">
        팀원 전체가 공유할 수 있는 소재 아카이빙 공간 마련하고 소재 선정 시 활용
      </li>
    </ul>
    <ul id="5273b7ce-2606-4505-b3cd-654937286b50" class="bulleted-list">
      <li style="list-style-type: disc">
        발송 후 여러 기기로 확인 하면서 가독성 테스트 진행
      </li>
    </ul>
    <p id="81a821d7-9704-48ae-af87-8e6d2a1f6e85" class=""></p>
    <h2
      id="418c5f68-333b-446e-bb02-736b1f6c4e3d"
      class="block-color-gray_background"
    >
      6️⃣ 고객 인터뷰
    </h2>
    <hr id="806ed502-12f3-49a8-8e6c-2da11d0040b6" />
    <h3
      id="4ad1baa0-243b-49f8-a63b-a414d7570cbd"
      class="block-color-yellow_background"
    >
      1) 인터뷰 진행
    </h3>
    <ul id="96c66144-0a7b-4c9c-bea4-710341dd0e2d" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong>3차 광고 전 고객 인터뷰 진행</strong>
        <ul id="7415d282-1599-4234-a67f-5af7c1bc2df5" class="bulleted-list">
          <li style="list-style-type: circle">
            일시 : 7/1 (금) - 7/6 (수) 진행
          </li>
        </ul>
        <ul id="ce2e22e7-d528-4a53-a7a6-310e4f9cd01b" class="bulleted-list">
          <li style="list-style-type: circle">인원 수 : 총 6명</li>
        </ul>
        <ul id="a05f2bf9-3990-4dc0-8fa5-1693a5ca0ed3" class="bulleted-list">
          <li style="list-style-type: circle">
            유입 경로 : 지인, 오픈채팅방, 코드스테이츠
          </li>
        </ul>
      </li>
    </ul>
    <figure id="1f6d623b-9eec-4ced-a569-87df0a45df01" class="link-to-page">
      <a href="https://www.notion.so/1f6d623b9eec4ceda56987df0a45df01"
        ><span class="icon">📝</span>인터뷰 질문 구성</a
      >
    </figure>
    <ul id="d3a35c3e-1dc8-465a-9ad6-94083331bafd" class="toggle">
      <li>
        <details open="">
          <summary><strong>인터뷰 질문 구성 </strong></summary>
          <table id="379f3846-8c41-4c6c-a2b1-70897adef303" class="simple-table">
            <tbody>
              <tr id="9808da24-50f2-4c59-95a7-0d80f5c0fc2d">
                <td id="JTJh" class="" style="width: 266.98577880859375px">
                  <strong>질문 세트</strong>
                </td>
                <td id="iJtK" class="" style="width: 403.9829406738281px">
                  <strong>질문 세트 구성 이유</strong>
                </td>
              </tr>
              <tr id="4d15fd40-a7c2-490f-ac0d-57ef0313014f">
                <td id="JTJh" class="" style="width: 266.98577880859375px">
                  <strong>1 서비스 경험 전, 술 관련 경험 및 관심</strong>
                </td>
                <td id="iJtK" class="" style="width: 403.9829406738281px">
                  오마이술 서비스가 필요한 상황 파악 앞으로 제공할 서비스 방향
                  고민
                </td>
              </tr>
              <tr id="93ed3443-2de0-4362-92cf-a803498259d1">
                <td id="JTJh" class="" style="width: 266.98577880859375px">
                  <strong>2 오마이술 유입 경로+구독하게 된 계기</strong>
                </td>
                <td id="iJtK" class="" style="width: 403.9829406738281px">
                  고객이 흥미를 보이는 광고 소재와 기대하는 서비스 파악
                  랜딩페이지 불편한 점 파악, 수정된 랜딩페이지에 대한 피드백
                </td>
              </tr>
              <tr id="d1c32c7a-c97e-487a-97c1-22781e3bb680">
                <td id="JTJh" class="" style="width: 266.98577880859375px">
                  <strong>3 서비스 경험 - 뉴스레터 콘텐츠</strong>
                </td>
                <td id="iJtK" class="" style="width: 403.9829406738281px">
                  고객이 기대하는 바를 충족하는지 파악하고, 고객이 흥미롭게 본
                  콘텐츠를 파악하여 이후 뉴스레터 콘텐츠 소재를 기획하기 위하여
                </td>
              </tr>
              <tr id="5fea3f09-e61c-4cf0-85f9-d6d509e4ceb5">
                <td id="JTJh" class="" style="width: 266.98577880859375px">
                  <strong>4 전반적인 만족도 파악</strong>
                </td>
                <td id="iJtK" class="" style="width: 403.9829406738281px">
                  만족도를 1~5로 간단하게 표현하여, 고객이 만족도와 관련한
                  내용을 직접적으로 언급하지 않아도 어떻게 느끼는지를 파악하기
                  위함
                </td>
              </tr>
              <tr id="a2b5b9ef-e58c-401d-bce3-eaa2d60e1e59">
                <td id="JTJh" class="" style="width: 266.98577880859375px">
                  <strong>5 인터뷰 질문 외 추가로 피드백/느낀 점을 수집</strong>
                </td>
                <td id="iJtK" class="" style="width: 403.9829406738281px">
                  인터뷰 질문 구성에서 놓친 부분을 수집하기 위함
                </td>
              </tr>
            </tbody>
          </table>
          <p id="a2a2b5b0-f2e5-4a24-99c1-8b49218d4cb0" class=""></p>
        </details>
      </li>
    </ul>
    <h3
      id="1a9517d8-4c8b-4e4b-9593-12f19ebe83c4"
      class="block-color-yellow_background"
    >
      2) 문제점 도출
    </h3>
    <ul id="6da73db8-6c95-45fb-9db8-469b3f30b4bd" class="toggle">
      <li>
        <details open="">
          <summary>
            <strong>서비스 방향성 (판매처 정보에 대한 니즈 파악)</strong>
          </summary>
          <ul id="ae0288c8-3c95-4f17-b76d-2e2bf059b476" class="bulleted-list">
            <li style="list-style-type: disc">
              이색 주류를 구매하고 싶지만
              <strong>정확한 판매처 정보 부족</strong>
            </li>
          </ul>
          <ul id="475843ac-a2ef-4af0-a100-a7d590f9b0f9" class="bulleted-list">
            <li style="list-style-type: disc">
              전통주의 경우, <strong>최저가 사이트를 알려줬음 좋겠음</strong>.
            </li>
          </ul>
          <ul id="4013416d-f8fc-48c1-b20c-276e12a978cb" class="bulleted-list">
            <li style="list-style-type: disc">
              끊임없이 쏟아지는 신상 주류에 대한 맛이 궁금하지만 직접 찾아보기
              귀찮다.
              <ul
                id="ff1eda6c-eff6-47c1-9009-44593b975d9d"
                class="bulleted-list"
              >
                <li style="list-style-type: circle">
                  특히 맥주의 경우 종류가 다양하기 때문에 맛을 판별하고 고르기가
                  어려움
                </li>
              </ul>
            </li>
          </ul>
          <ul id="2aa54140-260d-4982-b428-2ebb284061dd" class="bulleted-list">
            <li style="list-style-type: disc">
              주류에 대한 정보가 방대하고 흩어져 있어 어떤 걸 선별해야할지 잘
              모르겠음. 누가 딱 짚어서 추천해줬음 좋겠다.
            </li>
          </ul>
          <ul id="814b32f1-95bd-4d67-8336-d99e87f2ae39" class="bulleted-list">
            <li style="list-style-type: disc">
              이색 주류(사케) 매니아들을 위한 정보 및 후기 콘텐츠가 다양했으면
              좋겠다.
            </li>
          </ul>
          <p id="31c09a0e-4470-40d1-ab0e-9d94b935eb55" class=""></p>
        </details>
      </li>
    </ul>
    <ul id="ddf0ec93-eb41-4df5-a74c-b5a8a307b14c" class="toggle">
      <li>
        <details open="">
          <summary>
            <strong>뉴스레터 콘텐츠 (콘텐츠의 다양성에 대한 니즈 파악)</strong>
          </summary>
          <ul id="b82b2db1-bf47-4542-971f-2fcb21243208" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong
                >행사 관련 콘텐츠를 진행하게 된다면 미리 행사가 있음을
                뉴스레터에서 알려주면 좋을 것 같음.</strong
              >
            </li>
          </ul>
          <ul id="e69d33f6-826a-44dd-be4f-b394033dd0f1" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong>공감할 수 있는 내용의 콘텐츠가 많았으면 좋겠음.</strong>
            </li>
          </ul>
          <ul id="5fd5702d-1a0f-4582-a217-ee970f709f44" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong
                >입문자들에게 어려운 칵테일바 이용방법에 대해 설명해주는 부분이
                생기면 좋을 것 같음.</strong
              >
            </li>
          </ul>
          <ul id="1d9bb213-fca9-4639-9780-df07aae09057" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong>하단에 다음에 소개될 주제를 미리 알려주면 좋겠다</strong>.
            </li>
          </ul>
          <ul id="330809af-c415-4021-9aa5-f22e4d75e0dc" class="bulleted-list">
            <li style="list-style-type: disc">
              리큐르나 칵테일, 양주에 대한 진입장벽이 있으니 용어에 대해 설명이
              있으면 좋을 거 같다.
            </li>
          </ul>
          <ul id="b1d83f1f-e528-48a7-9e90-a04e7174e8f7" class="bulleted-list">
            <li style="list-style-type: disc">
              쉐이커 사용법이나 칵테일 만들 때 필요한 준비물 등 안내가 있으면
              좋을 것 같음.
            </li>
          </ul>
          <ul id="30bfd754-4828-474b-b24f-f3c60bf1defa" class="bulleted-list">
            <li style="list-style-type: disc">
              뉴스레터에서 제공되는 이미지가 흐릿했던 것이 아쉬웠음.
            </li>
          </ul>
          <ul id="e7f455c6-30d1-470f-9344-4c06dc5ca06c" class="bulleted-list">
            <li style="list-style-type: disc">
              에디터의 음주 성향이 다른 것이 잘 드러나지 않음
            </li>
          </ul>
          <p id="4cac26a4-0ce1-42b8-845f-aca353f4e2e2" class=""></p>
        </details>
      </li>
    </ul>
    <ul id="6b630696-b7ec-440d-bc00-f0e099e5d1d9" class="toggle">
      <li>
        <details open="">
          <summary><strong>뉴스레터 형식 (ui 개선)</strong></summary>
          <ul id="63a05fa4-ad29-4a18-ad6b-e6f831173500" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong>뉴스레터 상단 카카오톡 대화의 가독성이 좋지 않음</strong>
            </li>
          </ul>
          <ul id="19da0233-be62-49dd-adea-3044f19cd435" class="bulleted-list">
            <li style="list-style-type: disc">일주일에 2번은 많다고 느껴짐</li>
          </ul>
          <ul id="66f6fd86-c664-4ec8-aa1b-29b017ade9eb" class="bulleted-list">
            <li style="list-style-type: disc">내용이 길다고 느껴짐</li>
          </ul>
          <ul id="66556b65-f5e0-411f-af50-8cf5af7ad921" class="bulleted-list">
            <li style="list-style-type: disc">
              조금 더 간결해도 좋을 것 같음. 관심 없는 주종의 경우 글이 많으면
              지루함
            </li>
          </ul>
          <ul id="fd4a9931-d6a9-4728-bd90-65c75305cc2d" class="bulleted-list">
            <li style="list-style-type: disc">
              콘텐츠마다 카테고리가 있으면 좋을 것 같음. 내용 구분이 어려움
            </li>
          </ul>
          <p id="ff10e90a-7684-411f-b46b-baf828d64a04" class=""></p>
        </details>
      </li>
    </ul>
    <ul id="5b1eedf0-99f6-47f2-bbde-1ce65c082847" class="toggle">
      <li>
        <details open="">
          <summary><strong>랜딩페이지 (UI 개선)</strong></summary>
          <ul id="05b320ca-cff9-47ea-a5cb-b22f32575db2" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong
                >썸네일, 문구 등 특별히 눈길을 잡아끄는 부분이 없음
              </strong>
            </li>
          </ul>
          <ul id="be39d99a-a421-4192-9876-f0f07a415479" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong
                >1차 랜딩페이지와 비교하였을 때, 소재(술) 관련 느낌이
                덜하다.</strong
              >
            </li>
          </ul>
          <ul id="ecd327c1-2d41-471a-9b5b-e65fed7f5995" class="bulleted-list">
            <li style="list-style-type: disc">
              <strong
                >랜딩페이지에서 구독자 = 대표님이라는 설정이 잘
                안보인다.</strong
              >
            </li>
          </ul>
          <ul id="6f51ed90-774b-428b-9dad-403f1256b88a" class="bulleted-list">
            <li style="list-style-type: disc">
              수요일 금요일 콘텐츠의 특징이 적힌 부분이 잘 보이지 않아 콘텐츠
              차별성 인지도 부족
            </li>
          </ul>
          <ul id="e9016ee8-bd4b-4f19-a81d-e280f59471ce" class="bulleted-list">
            <li style="list-style-type: disc">
              리뉴얼 이후 깔끔하지만 뉴스레터가 가지는 정겨움/친근함이 없어짐
            </li>
          </ul>
        </details>
      </li>
    </ul>
    <ul id="a31aeb1a-71b1-4b6a-89c6-d42ab24a3708" class="toggle">
      <li>
        <details open="">
          <summary><strong>기타(인스타그램 활성)</strong></summary>
          <ul id="1fc8de96-2f6d-4040-a29c-cde9ee2d9e99" class="bulleted-list">
            <li style="list-style-type: disc">
              인스타그램에 뉴스레터와 관련된 게시물이 업데이트 되면 좋겠음
            </li>
          </ul>
          <ul id="d829c7fb-6c44-4cdb-a878-39d3dc3090c9" class="bulleted-list">
            <li style="list-style-type: disc">
              인스타그램을 활성화 시켰음 좋겠다. 구독자 이벤트와 같이 구독자와
              소통하는 창구로 확장시켰음 좋겠음
            </li>
          </ul>
        </details>
      </li>
    </ul>
    <h3
      id="ec9af1b5-9124-48c0-9771-a3bb7fac7319"
      class="block-color-yellow_background"
    >
      3) 개선안 도출
    </h3>
    <ul id="4110e432-a61b-4357-85cd-b587b1253be6" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong>뉴스레터(판매 정보 및 다양한 콘텐츠 추가/UI개선)</strong>
      </li>
    </ul>
    <p id="4beb7e09-db82-4537-85af-66238da6e308" class=""></p>
    <p id="901a2bb9-2800-417a-a9c3-975f45515829" class="">콘텐츠 추가</p>
    <div class="indented">
      <ul id="1bc008d5-e70b-4b0f-8399-a6a241e6d6a2" class="bulleted-list">
        <li style="list-style-type: disc">
          술의 가격, 구매할 수 있는 구매처를 기입 ㄴ (ex.술담화, 스마트오더 등
          다양한 구매방법을 뉴스레터 하단에 지속적으로 안내)
        </li>
      </ul>
      <ul id="5eb32107-762d-4d97-ab26-8f414916936d" class="bulleted-list">
        <li style="list-style-type: disc">
          스낵콘텐츠로 칵테일바 이용방법이나 오늘의 칵테일 등을 제작
        </li>
      </ul>
      <ul id="4fa86d90-8fb3-4c58-92c3-2cb91bcabdca" class="bulleted-list">
        <li style="list-style-type: disc">하단에 다음 주제 예고 안내</li>
      </ul>
      <ul id="8a8ea55a-a6f1-4595-9e8d-eab209257503" class="bulleted-list">
        <li style="list-style-type: disc">
          행사 관련 콘텐츠를 진행하면, 이전 주에 다음주 콘텐츠에 있을 행사를
          안내
        </li>
      </ul>
      <p id="61304212-19cc-4854-bcdc-32e2a6f51b0f" class="">UI 개선</p>
      <ul id="81c3a736-de3b-4c5b-b99d-7746393d3fd1" class="bulleted-list">
        <li style="list-style-type: disc">
          상단 카카오톡 대화 이미지 크기 조정 및 글자 조정
        </li>
      </ul>
      <ul id="298833ec-37fd-46bc-986c-4dd3ace2033b" class="bulleted-list">
        <li style="list-style-type: disc">모바일 버전 글자 크기 확대</li>
      </ul>
    </div>
    <p></p>
    <p id="3e50fb60-f147-4cd9-8ecf-3c3adf8df47d" class=""></p>
    <ul id="ca0c3947-0342-4ccc-b0bf-f64b2344a384" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong>랜딩 페이지(UI개선)</strong>
        <ul id="6c379e2e-7244-4ff5-856e-c5a82392cfa2" class="bulleted-list">
          <li style="list-style-type: circle">
            지난 뉴스레터 페이지 콘텐츠 썸네일을 좀 더 직관적으로 수정.
          </li>
        </ul>
        <ul id="5ef468ba-44a4-4b19-a37f-3ae733caea6b" class="bulleted-list">
          <li style="list-style-type: circle">
            서비스 주요 카피 강조 및 보완 (눈에 띄게 직관적으로)
          </li>
        </ul>
        <ul id="47e87f49-fdf1-46f1-9835-3d54d1c78ef4" class="bulleted-list">
          <li style="list-style-type: circle">
            소개란에서 뉴스레터가 오마이술 직원이 대표님에게 보내는 ‘보고서’라는
            콘셉트를 직관적으로 보이도록 이미지를 활용해서 보완
          </li>
        </ul>
        <ul id="5827b1d9-ca3e-43e1-916c-bbc4dc2ff962" class="bulleted-list">
          <li style="list-style-type: circle">
            메인페이지 상단 이미지를 좀 더 친근하고 술의 이미지가 연상되는
            이미지로 변경
          </li>
        </ul>
        <p id="fd1f3721-526d-4685-9942-d384d0f3b4ac" class=""></p>
      </li>
    </ul>
    <ul id="4317b16c-2bcb-4a47-82e9-eb8e740afb08" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong>기타(인스타그램 채널 활성화)</strong>
        <ul id="bf17c012-1fb8-4c7c-aeb1-c42016e9abfe" class="bulleted-list">
          <li style="list-style-type: circle">
            뉴스레터 홍보 콘텐츠 및 스낵콘텐츠 주기적으로 업로드
          </li>
        </ul>
        <ul id="1e99bfd4-f488-46a2-8f72-bbc1123a8aaf" class="bulleted-list">
          <li style="list-style-type: circle">
            유입자가 증가하면 인스타그램을 통한 구독자 이벤트 실시
          </li>
        </ul>
      </li>
    </ul>
    <p id="b35ea085-4b0c-4ae9-967b-0984495f8131" class=""></p>
    <h3
      id="803dc19f-306d-4639-8f2d-0d2033d07d22"
      class="block-color-yellow_background"
    >
      4) 기타 의견 정리
    </h3>
    <ul id="e0f9aa7c-83c0-4903-9e95-335780d171cd" class="bulleted-list">
      <li style="list-style-type: disc">
        <strong>원하는 콘텐츠</strong>
        <ul id="17950429-9318-48a8-acef-b2e2f03be562" class="bulleted-list">
          <li style="list-style-type: circle">
            칵테일바 이용 방법을 재미있게 소개
          </li>
        </ul>
        <ul id="ef29cc32-fe0f-4333-819c-9d2200d20461" class="bulleted-list">
          <li style="list-style-type: circle">
            싱글몰트 안에서도 나눠지는 다양한 종류의 위스키 소개
          </li>
        </ul>
        <ul id="185144a2-8b0f-42bb-9445-4e7fbbe4c004" class="bulleted-list">
          <li style="list-style-type: circle">
            사케 관련 콘텐츠 카테고리 추가
          </li>
        </ul>
        <ul id="59daa0fe-fd3a-41f6-ab34-779427c0f2bc" class="bulleted-list">
          <li style="list-style-type: circle">
            에디터들의 술과 관련된 일화를 소개하는 스낵 콘텐츠 추가
          </li>
        </ul>
        <ul id="9372a8db-17af-42e6-aaf0-bae8d9d85185" class="bulleted-list">
          <li style="list-style-type: circle">
            에디터들의 단골 술집 소개 (술집 추천)
          </li>
        </ul>
        <ul id="0ab6c532-832b-41fb-a07f-07c02f16b7e2" class="bulleted-list">
          <li style="list-style-type: circle">
            이색 주류를 판매하고 있는 술집 아카이브
          </li>
        </ul>
        <ul id="50effe71-8825-4ff2-9ca3-d7c82e4d5090" class="bulleted-list">
          <li style="list-style-type: circle">
            사회 초년생들을 위한 회식/술자리 예절 및 매너 법을 재미있게
            소개하기.
          </li>
        </ul>
        <ul id="51548a48-fd8c-4b11-9830-de954401aee9" class="bulleted-list">
          <li style="list-style-type: circle">세계의 이색 술 소개</li>
        </ul>
        <ul id="2111de35-653d-4df9-9d2f-0634b5b993ce" class="bulleted-list">
          <li style="list-style-type: circle">
            극도의 알쓰 VS 극도의 고도수 러버의 대결 구도
          </li>
        </ul>
      </li>
    </ul>
    <h2
      id="15327e1f-bc49-43f5-af3e-c12739c6e911"
      class="block-color-gray_background"
    >
      7️⃣ 향후 계획
    </h2>
    <hr id="7bbdef78-8ce3-45c4-b446-796bce3ef2e2" />
    <h3
      id="d4481dae-e428-4d3c-9a42-aa8de538aa2a"
      class="block-color-yellow_background"
    >
      1) 앱 서비스 홈텐드와 협업 내용 및 일정 논의
    </h3>
    <ul id="debca9b2-eecb-4ef1-a50e-ff37b5d88720" class="bulleted-list">
      <li style="list-style-type: disc">
        1차 고객조사 시, 위스키, 칵테일의 가격정보 및 판매처를 검색할 수 있는
        주류 정보 검색 서비스인 홈텐드의 협업 요청 문의
      </li>
    </ul>
    <ul id="f7fe14d6-9f2b-4b84-a0f0-09f7b38a1cd4" class="bulleted-list">
      <li style="list-style-type: disc">
        랜딩 페이지 리뉴얼 및 뉴스레터 제작 이후 협업 제안서를 발송하여 추후
        협업 일정을 논의
        <p id="9a1d6b52-5042-4396-8195-9d97cc10a48b" class="">
          → 7월 13일 수요일 오후 7시 온라인 미팅 예정
        </p>
      </li>
    </ul>
    <ul id="d799b4d9-92d6-4abf-91a4-7e0d29263b72" class="bulleted-list">
      <li style="list-style-type: disc">
        협업 제안서
