    >
      2) 3차 광고 진행
    </h3>
    <ul id="ab71349a-57e5-4de0-80d8-1ebaa4230619" class="bulleted-list">
      <li style="list-style-type: disc">일정 : 2022.07.14~2022.7.21(7일)</li>
    </ul>
    <ul id="0c1f8478-4010-47bb-9026-0be22ceff8c0" class="bulleted-list">
      <li style="list-style-type: disc">매체 : FB&amp;IG, Google Ads</li>
    </ul>
    <ul id="b776d635-921f-462e-bcf1-82351b9d2a6a" class="bulleted-list">
      <li style="list-style-type: disc">
        소재
        <ul id="f9828728-8725-4763-8a64-8b60192c8a54" class="bulleted-list">
          <li style="list-style-type: circle">
            예산이 제한적이므로 2차 광고에서 성과가 좋았던 소재: 혼술, 가성비,
            하이볼, 꿀조합 레시피, 퀴즈관련 카피로 테스트 진행
          </li>
        </ul>
        <ul id="2f67dde4-1aa6-42d1-b07e-ef46748309d7" class="bulleted-list">
          <li style="list-style-type: circle">
            3차 USP를 살린 카피: 다양한 주종, 다양한 성향가진 에디터들의 솔직
            리뷰&amp;추천, 단순 제품 추천 뿐만 아니라 재밌는 술 콘텐츠, 입문자를
            위한 칵테일바 사용법
          </li>
        </ul>
        <ul id="13cd9813-d099-4683-9a41-4d91371d4baa" class="bulleted-list">
          <li style="list-style-type: circle">
            단순 호기심 해결이 아닌 술 콘텐츠에 흥미를 느낄 수 있을만한 참여형
            광고: MBTI, 성향으로 추측하는 술스타일
          </li>
        </ul>
        <ul id="dbc6e887-b99c-454c-9f8e-0bf41a3d93a0" class="bulleted-list">
          <li style="list-style-type: circle">
            정성조사 결과 응답자가 가장 많았던 안주에 대한 카피는 보류
          </li>
        </ul>
      </li>
    </ul>
