      3) 오프라인 바틀샵 협업
    </h3>
    <ul id="136b4f0b-f8dd-49f0-8997-edd0b9dc023c" class="bulleted-list">
      <li style="list-style-type: disc">일정 : 2022.07.14 이후</li>
    </ul>
    <ul id="c2af1146-7d46-4b3c-b41a-51cf8f46cdd9" class="bulleted-list">
      <li style="list-style-type: disc">
        대상 : 서울, 부산 등 오프라인 바틀샵
      </li>
    </ul>
    <ul id="e7156c32-1b56-43f1-8c03-4dbf41e00a59" class="toggle">
      <li>
        <details open="">
          <summary>협업 내용</summary>
          <ol
            type="1"
            id="ac0907d3-9b9d-4f22-ba7f-104241bbf677"
            class="numbered-list"
            start="1"
          >
            <li>
              오마이술은 바틀샵에서 판매하는 주류 큐레이션을 뉴스레터를 통해
              제안하고, 구매처를 알려주는 곳에 해당 매장을 소개. 바틀샵은
              오마이술 뉴스레터 구독자 유입이 되도록 QR 코드를 가게에 전시
            </li>
          </ol>
          <ol
            type="1"
            id="d1069201-d097-485f-b009-b1a2fbe8123b"
            class="numbered-list"
            start="2"
          >
            <li>
              매장 베스트 주류 큐레이션 또는 구매자 취향에 맞게 주류 구매 팁을
              알려주는 콘텐츠 제작 후 해당 매장에 배포.
            </li>
          </ol>
          <figure id="a9acf574-f9e5-434b-9438-88c915d3481c" class="image">
